.sidebar {
    height: 100vh;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}

.sidebar a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 18px;
    color: #818181;
    display: block;
    transition: 0.3s;
}

.sidebar a:hover {
    color: #f1f1f1;
}

.sidebar .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
}

.openbtn {
    margin-left: -20px;
    width: 20px;
    height: 20px;
    font-size: 10px;
    cursor: pointer;
    background-color: #111;
    color: white;
    border: none;
}

.openbtn:hover {
    background-color: #444;
}
.sidebarActive{
    width: 250px
}
.sidebarDefault{
    width: 0px
}
.mainActive{
    margin-left: 250px;
}
.mainDefault{
    margin-left: 0px;
}
.btnCollapsed{
display: none;
}
.btnDefault{
    display: inline-block;
}
.sidebar button{
    width: 15px;
    font-size: 15px;
    background-color: #111111;
    color: white;
    border: none
}

.main {
    transition: margin-left .5s;
    padding: 16px;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
    .sidebar {padding-top: 15px;}
    .sidebar a {font-size: 18px;}
}